.header {
  z-index: 100;
  position: relative;
}

.header__container {
  z-index: 100;
  background-color: #0c0b0b;
  justify-content: space-between;
  align-items: center;
  padding: 10px 0;
  display: flex;
}

.header__search-form {
  position: relative;
}

.header__search-form input {
  background-color: inherit;
  color: #fff;
  width: 200px;
  border: 1px solid rgba(250, 250, 250, .3);
  border-radius: 21px;
  padding: 12px 24px 12px 45px;
}

.header__search-form button {
  background-color: inherit;
  cursor: pointer;
  border: none;
  position: absolute;
  top: 30%;
  left: 20px;
}

.header__icon-search {
  fill: #fff;
}

@media screen and (max-width: 767px) {
  .header {
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    left: 0;
  }

  .header__logo img {
    width: 160px;
  }

  .header__search-form {
    width: 120px;
  }

  .header__search-form input {
    width: 130px;
    padding-top: 10px;
    padding-bottom: 10px;
    font-size: 14px;
  }
}

@media screen and (min-width: 768px) and (max-width: 1200px) {
  .header {
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    left: 0;
  }
}

@media screen and (min-width: 1200px) {
  .header__container {
    background-color: rgba(12, 11, 11, .7);
    position: fixed;
    left: 50%;
    transform: translate(-50%);
  }
}

.footer__container {
  background-color: #0c0b0b;
}

.footer__navigation {
  color: #fafafa;
  justify-content: space-between;
  align-items: center;
  padding: 32px 0;
  display: flex;
}

.footer__menu-list {
  gap: 20px;
  display: flex;
}

.footer__menu-list a:hover {
  color: rgba(250, 250, 250, .4);
}

.footer__copyright {
  color: rgba(250, 250, 250, .4);
  text-align: center;
  background-color: #34387f;
  padding: 18px;
}

.footer__social-list {
  gap: 16px;
  display: flex;
}

.footer__social-list-item {
  width: 40px;
  height: 40px;
  cursor: pointer;
  border: 1px solid rgba(250, 250, 250, .15);
  border-radius: 6px;
  justify-content: center;
  align-items: center;
  display: flex;
}

.footer__social-list-link {
  height: 20px;
}

.footer__social-list-icon {
  width: 20px;
  height: 20px;
  fill: #fafafa;
}

.footer__social-list-item:hover .footer__social-list-icon {
  fill: rgba(250, 250, 250, .4);
}

@media screen and (max-width: 767px) {
  .footer__navigation {
    flex-direction: column;
    gap: 40px;
  }
}

